body{margin:100;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.container{padding:3px;margin:20px;margin-top:30px}input[type="text"]{position:relative;top:10px;min-width:95%;font-size:6px;height:30px;border:1px solid #a2a9ac;border-radius:40px;margin:10px;font-size:15px;padding:8px 10px}input[type="text"]:focus{outline:none;border:2px solid #a2a9ac}.border,.division>.sec>*{border:1px solid pink}.text-pad{display:flex;background-color:#a8d6d8;height:40px;border:1px solid pink;border-radius:5px}.text-pad>*{flex:0 1 10%;padding-top:5px}.division{display:flex;margin-top:10px}.division>.first{flex:0 1 70%;padding-top:5px}.division>.sec{margin:3px 0 0 9px;flex:0 1 30%;display:flex;justify-content:space-evenly;flex-flow:column nowrap}.division>.sec>*{padding:10px 7px;flex:0 1 20%}.quantity{width:15px;font-size:16px}.min{position:relative;right:31.5px;top:7px}.cart-items{display:flex;flex-wrap:nowrap}.spanner>span,.spinner>span,.spinner>.plus>span,.spinner>.minus>span{position:relative;top:-10px}.spinner{margin-top:10px;width:40px;height:27px;border:1px solid red}.spinner>.plus{position:relative;top:2px;height:10px;width:10px;border:1px solid red}.spinner>.minus{position:relative;top:-20px;height:10px;width:10px;border:1px solid red}.flex-float,.out>.title,.out>.content{display:flex;justify-content:space-between}.out{display:flex;flex-flow:column wrap}.out>.title{padding:10px;font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;font-size:16px}.out>.content{margin:10px;padding-bottom:10px;border-bottom:1px solid silver;text-align:left}.out>.content>.rate{text-align:right}
